html {
  /* rem em 相对单位 */
  /* em 相对于父元素的 font-size */
  /* rem 相对于根元素 html 的 font-size, r 就是 root 的意思 */
  /* 1rem == 一个 font-size 的单位 1rem ==
   10px */
  /* 浏览器默认的 font-size 是 16px 16*62.5% = 10px */
  font-size: 62.5%;
}

html body #root .App {
  /* viewport height === vh 视口 1vh === 1% 视口高度 */
  min-height: 100vh;
}